import Link from "next/link";

import { Component } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  Button
} from "reactstrap";

import styled from "styled-components";

import navbarBrandLogo from "../static/images/logo.png";

const NavBarDiv = styled.div`
  .navbar-nav {
    float: none;
    margin: 0 auto;
    display: block;
    text-align: center;

    a {
      font-weight: 800;
      font-size: 200%;
    }
  }

  .navbar-nav > li {
    display: block;
    float: none;
  }

  @media (min-width: 768px) {
    .navbar-nav {
      float: none;
      margin: 0 auto;
      display: block;
      text-align: center;
    }

    .navbar-nav > li {
      display: inline-block;
      float: none;
    }
  }

  .navbarBrandLogo {
    position: relative;
    top: 0;
    left: 0;
    img {
      width: 5vw;
    }
  }
`;

export default class Navbar1 extends Component {
  constructor(props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true
    };
  }

  toggleNavbar() {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }

  render() {
    return (
      <NavBarDiv>
        <Navbar color="faded" dark expand="lg" className="">
          <NavbarBrand
            href={process.env.BACKEND_URL + "/"}
            className="navbarBrandLogo mr-auto"
          >
            <img
              src={navbarBrandLogo}
              alt="Santa & the Mrs of West Texas Logo"
            />
          </NavbarBrand>
          <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
          <Collapse isOpen={!this.state.collapsed} navbar>
            <Nav navbar className="text-success">
              <NavItem>
                <Link href="/" as={process.env.BACKEND_URL + "/"}>
                  <a className="nav-link">
                    <Button type="button" class="btn btn-default navbar-btn">
                      Home
                    </Button>
                  </a>
                </Link>
              </NavItem>
              <NavItem>
                <Link
                  prefetch
                  href="/about"
                  as={process.env.BACKEND_URL + "/about"}
                >
                  <a className="nav-link">
                    <Button type="button" class="btn btn-default navbar-btn">
                      About
                    </Button>
                  </a>
                </Link>
              </NavItem>
              <NavItem>
                <Link
                  prefetch
                  href="/calendar"
                  as={process.env.BACKEND_URL + "/calendar"}
                >
                  <a className="nav-link">
                    <Button type="button" class="btn btn-default navbar-btn">
                      Calendar
                    </Button>
                  </a>
                </Link>
              </NavItem>
              <NavItem>
                <Link
                  prefetch
                  href="/contact"
                  as={process.env.BACKEND_URL + "/contact"}
                >
                  <a className="nav-link">
                    <Button type="button" class="btn btn-default navbar-btn">
                      Contact
                    </Button>
                  </a>
                </Link>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </NavBarDiv>
    );
  }
}
